import React, { useState } from 'react';
import { Card, Row, Col, Button, Form, Table, Alert, Badge, Modal } from 'react-bootstrap';
import { useForm } from 'react-hook-form';
import api from '../services/api';

const CompareSchemes = ({ onError, initialSchemes = [] }) => {
  const [schemes, setSchemes] = useState(initialSchemes);
  const [comparisonResult, setComparisonResult] = useState(null);
  const [loading, setLoading] = useState(false);
  const [showModal, setShowModal] = useState(false);

  const { register, handleSubmit, reset, formState: { errors } } = useForm();

  // 当外部传入方案时更新
  React.useEffect(() => {
    if (initialSchemes.length > 0) {
      setSchemes(initialSchemes);
    }
  }, [initialSchemes]);

  const addScheme = (data) => {
    const years = parseFloat(data.years) || 3;
    const frequency = parseInt(data.frequency) || 12;
    const periods = years * frequency;

    const newScheme = {
      id: Date.now(),
      name: data.name,
      method: data.method,
      params: {
        pv: parseFloat(data.pv),
        annual_rate: parseFloat(data.annual_rate) / 100, // 转换百分比
        periods: periods, // 使用计算的期数
        frequency: frequency,
        guarantee: parseFloat(data.guarantee) || 0,
        guarantee_mode: data.guarantee_mode || '尾期冲抵',
        years: years
      }
    };

    if (schemes.length >= 5) {
      onError('最多只能添加5个对比方案');
      return;
    }

    setSchemes([...schemes, newScheme]);
    setShowModal(false);
    reset();
  };

  // 从外部添加方案的方法
  const addSchemeFromExternal = (schemeData) => {
    const methodNames = {
      'equal_annuity': '等额年金法',
      'equal_principal': '等额本金法',
      'flat_rate': '平息法',
      'floating_rate': '浮动利率法'
    };

    const newScheme = {
      id: Date.now(),
      name: `方案${schemes.length + 1}（${methodNames[schemeData.method]}）`,
      method: schemeData.method,
      params: schemeData
    };

    if (schemes.length >= 5) {
      onError('最多只能添加5个对比方案');
      return false;
    }

    setSchemes([...schemes, newScheme]);
    return true;
  };

  // 暴露给父组件的方法
  React.useImperativeHandle(React.forwardRef(() => null), () => ({
    addScheme: addSchemeFromExternal
  }));

  const removeScheme = (id) => {
    setSchemes(schemes.filter(scheme => scheme.id !== id));
    if (schemes.length <= 1) {
      setComparisonResult(null);
    }
  };

  const compareSchemes = async () => {
    if (schemes.length < 2) {
      onError('至少需要2个方案才能进行对比');
      return;
    }

    setLoading(true);
    try {
      const response = await api.post('/compare', { schemes });
      setComparisonResult(response.data.data);
    } catch (error) {
      onError(error.response?.data?.message || '对比计算失败');
    } finally {
      setLoading(false);
    }
  };

  const formatCurrency = (amount) => {
    return new Intl.NumberFormat('zh-CN', {
      style: 'currency',
      currency: 'CNY',
      minimumFractionDigits: 2
    }).format(amount || 0);
  };

  const formatPercent = (rate) => {
    return `${((rate || 0) * 100).toFixed(4)}%`;
  };

  const getMethodLabel = (method) => {
    const labels = {
      'equal_annuity': '等额年金法',
      'equal_principal': '等额本金法',
      'flat_rate': '平息法',
      'floating_rate': '浮动利率法'
    };
    return labels[method] || method;
  };

  const getBestScheme = (metric) => {
    if (!comparisonResult || comparisonResult.length === 0) return null;
    
    let bestIndex = 0;
    let bestValue = comparisonResult[0][metric];

    comparisonResult.forEach((result, index) => {
      if (metric === 'total_interest' || metric === 'total_payment') {
        // 越小越好
        if (result[metric] < bestValue) {
          bestValue = result[metric];
          bestIndex = index;
        }
      } else if (metric === 'irr') {
        // 越大越好（对承租人而言是越小越好，但这里显示的是租赁公司视角）
        if (result[metric] > bestValue) {
          bestValue = result[metric];
          bestIndex = index;
        }
      }
    });

    return bestIndex;
  };

  return (
    <div>
      {/* 方案管理 */}
      <Card className="mb-4">
        <Card.Header className="d-flex justify-content-between align-items-center">
          <h5 className="mb-0">
            <i className="fas fa-layer-group me-2"></i>
            方案管理
          </h5>
          <Button 
            variant="primary" 
            size="sm"
            onClick={() => setShowModal(true)}
            disabled={schemes.length >= 5}
          >
            <i className="fas fa-plus me-1"></i>
            添加方案
          </Button>
        </Card.Header>
        <Card.Body>
          {schemes.length === 0 ? (
            <Alert variant="info" className="text-center mb-0">
              <i className="fas fa-info-circle fa-2x mb-3"></i>
              <h6>暂无对比方案</h6>
              <p className="mb-0">点击"添加方案"开始创建对比方案</p>
            </Alert>
          ) : (
            <Row>
              {schemes.map((scheme) => (
                <Col md={6} lg={4} key={scheme.id} className="mb-3">
                  <Card className="scheme-card h-100">
                    <Card.Header className="d-flex justify-content-between align-items-center">
                      <h6 className="mb-0">{scheme.name}</h6>
                      <Button
                        variant="outline-danger"
                        size="sm"
                        onClick={() => removeScheme(scheme.id)}
                      >
                        <i className="fas fa-trash"></i>
                      </Button>
                    </Card.Header>
                    <Card.Body>
                      <div className="mb-2">
                        <Badge bg="info">{getMethodLabel(scheme.method)}</Badge>
                      </div>
                      <Table borderless size="sm">
                        <tbody>
                          <tr>
                            <td>租赁本金：</td>
                            <td>{formatCurrency(scheme.params.pv)}</td>
                          </tr>
                          <tr>
                            <td>年利率：</td>
                            <td>{(scheme.params.annual_rate * 100).toFixed(2)}%</td>
                          </tr>
                          <tr>
                            <td>期限：</td>
                            <td>{scheme.params.periods}期</td>
                          </tr>
                          {scheme.params.guarantee > 0 && (
                            <>
                              <tr>
                                <td>保证金：</td>
                                <td>{formatCurrency(scheme.params.guarantee)}</td>
                              </tr>
                              <tr>
                                <td>处理方式：</td>
                                <td><Badge bg="secondary">{scheme.params.guarantee_mode}</Badge></td>
                              </tr>
                            </>
                          )}
                        </tbody>
                      </Table>
                    </Card.Body>
                  </Card>
                </Col>
              ))}
            </Row>
          )}
          
          {schemes.length >= 2 && (
            <div className="text-center mt-3">
              <Button
                variant="success"
                size="lg"
                onClick={compareSchemes}
                disabled={loading}
              >
                {loading ? (
                  <>
                    <i className="fas fa-spinner fa-spin me-2"></i>
                    对比计算中...
                  </>
                ) : (
                  <>
                    <i className="fas fa-balance-scale me-2"></i>
                    开始对比
                  </>
                )}
              </Button>
            </div>
          )}
        </Card.Body>
      </Card>

      {/* 对比结果 */}
      {comparisonResult && (
        <Card>
          <Card.Header>
            <h5 className="mb-0">
              <i className="fas fa-chart-bar me-2"></i>
              对比结果
            </h5>
          </Card.Header>
          <Card.Body>
            <div className="table-responsive">
              <Table striped bordered hover>
                <thead className="table-primary">
                  <tr>
                    <th>方案名称</th>
                    <th>计算方法</th>
                    <th>每期租金</th>
                    <th>总利息</th>
                    <th>总支付</th>
                    <th>IRR</th>
                    <th>综合评价</th>
                  </tr>
                </thead>
                <tbody>
                  {comparisonResult.map((result, index) => {
                    const isBestPayment = getBestScheme('pmt') === index;
                    const isBestInterest = getBestScheme('total_interest') === index;
                    const isBestTotal = getBestScheme('total_payment') === index;
                    const isBestIRR = getBestScheme('irr') === index;

                    return (
                      <tr key={index}>
                        <td>
                          <strong>{result.scheme_name}</strong>
                        </td>
                        <td>
                          <Badge bg="secondary">
                            {getMethodLabel(result.method)}
                          </Badge>
                        </td>
                        <td className={isBestPayment ? 'table-success' : ''}>
                          {formatCurrency(result.pmt)}
                          {isBestPayment && <i className="fas fa-crown text-warning ms-1"></i>}
                        </td>
                        <td className={isBestInterest ? 'table-success' : ''}>
                          {formatCurrency(result.total_interest)}
                          {isBestInterest && <i className="fas fa-crown text-warning ms-1"></i>}
                        </td>
                        <td className={isBestTotal ? 'table-success' : ''}>
                          {formatCurrency(result.total_payment)}
                          {isBestTotal && <i className="fas fa-crown text-warning ms-1"></i>}
                        </td>
                        <td className={isBestIRR ? 'table-success' : ''}>
                          {formatPercent(result.irr)}
                          {isBestIRR && <i className="fas fa-crown text-warning ms-1"></i>}
                        </td>
                        <td>
                          {(isBestPayment || isBestInterest || isBestTotal) && (
                            <Badge bg="success">推荐</Badge>
                          )}
                        </td>
                      </tr>
                    );
                  })}
                </tbody>
              </Table>
            </div>

            {/* 对比分析 */}
            <Alert variant="info">
              <Alert.Heading>
                <i className="fas fa-lightbulb me-2"></i>
                对比分析
              </Alert.Heading>
              <Row>
                <Col md={6}>
                  <h6>成本最优方案：</h6>
                  <p>
                    最低每期租金：{comparisonResult[getBestScheme('pmt')]?.scheme_name}<br/>
                    最低总利息：{comparisonResult[getBestScheme('total_interest')]?.scheme_name}<br/>
                    最低总支付：{comparisonResult[getBestScheme('total_payment')]?.scheme_name}
                  </p>
                </Col>
                <Col md={6}>
                  <h6>收益率分析：</h6>
                  <p>
                    最高IRR方案：{comparisonResult[getBestScheme('irr')]?.scheme_name}<br/>
                    IRR差异范围：{formatPercent(Math.max(...comparisonResult.map(r => r.irr)) - Math.min(...comparisonResult.map(r => r.irr)))}
                  </p>
                </Col>
              </Row>
            </Alert>
          </Card.Body>
        </Card>
      )}

      {/* 添加方案模态框 */}
      <Modal show={showModal} onHide={() => setShowModal(false)} size="lg">
        <Modal.Header closeButton>
          <Modal.Title>添加对比方案</Modal.Title>
        </Modal.Header>
        <Form onSubmit={handleSubmit(addScheme)}>
          <Modal.Body>
            <Row className="mb-3">
              <Col md={6}>
                <Form.Label>方案名称</Form.Label>
                <Form.Control
                  type="text"
                  {...register('name', { required: '请输入方案名称' })}
                  isInvalid={!!errors.name}
                  placeholder="例如：方案A"
                />
                {errors.name && (
                  <Form.Control.Feedback type="invalid">
                    {errors.name.message}
                  </Form.Control.Feedback>
                )}
              </Col>
              <Col md={6}>
                <Form.Label>计算方法</Form.Label>
                <Form.Select
                  {...register('method', { required: '请选择计算方法' })}
                  isInvalid={!!errors.method}
                >
                  <option value="">请选择...</option>
                  <option value="equal_annuity">等额年金法</option>
                  <option value="equal_principal">等额本金法</option>
                  <option value="flat_rate">平息法</option>
                </Form.Select>
                {errors.method && (
                  <Form.Control.Feedback type="invalid">
                    {errors.method.message}
                  </Form.Control.Feedback>
                )}
              </Col>
            </Row>

            <Row className="mb-3">
              <Col md={6}>
                <Form.Label>租赁本金（元）</Form.Label>
                <Form.Control
                  type="number"
                  step="0.01"
                  {...register('pv', { 
                    required: '请输入租赁本金',
                    min: { value: 1, message: '租赁本金必须大于0' }
                  })}
                  isInvalid={!!errors.pv}
                />
                {errors.pv && (
                  <Form.Control.Feedback type="invalid">
                    {errors.pv.message}
                  </Form.Control.Feedback>
                )}
              </Col>
              <Col md={6}>
                <Form.Label>年利率</Form.Label>
                <Form.Control
                  type="number"
                  step="0.01"
                  {...register('annual_rate', { 
                    required: '请输入年利率',
                    min: { value: 0.01, message: '年利率必须大于0' },
                    max: { value: 100, message: '年利率不能超过100%' }
                  })}
                  isInvalid={!!errors.annual_rate}
                  placeholder="例如：8.00 (%)"
                />
                {errors.annual_rate && (
                  <Form.Control.Feedback type="invalid">
                    {errors.annual_rate.message}
                  </Form.Control.Feedback>
                )}
              </Col>
            </Row>

            <Row className="mb-3">
              <Col md={6}>
                <Form.Label>支付频率</Form.Label>
                <Form.Select
                  {...register('frequency', { required: '请选择支付频率' })}
                  isInvalid={!!errors.frequency}
                >
                  <option value="">请选择...</option>
                  <option value="12">月付</option>
                  <option value="4">季付</option>
                  <option value="2">半年付</option>
                  <option value="1">年付</option>
                </Form.Select>
                {errors.frequency && (
                  <Form.Control.Feedback type="invalid">
                    {errors.frequency.message}
                  </Form.Control.Feedback>
                )}
              </Col>
              <Col md={6}>
                <Form.Label>租赁年数</Form.Label>
                <Form.Control
                  type="number"
                  step="0.1"
                  {...register('years', { 
                    required: '请输入租赁年数',
                    min: { value: 0.1, message: '租赁年数至少为0.1年' }
                  })}
                  isInvalid={!!errors.years}
                  placeholder="例如：3.0"
                />
                {errors.years && (
                  <Form.Control.Feedback type="invalid">
                    {errors.years.message}
                  </Form.Control.Feedback>
                )}
              </Col>
            </Row>

            <Row className="mb-3">
              <Col md={6}>
                <Form.Label>保证金金额（元）</Form.Label>
                <Form.Control
                  type="number"
                  step="0.01"
                  {...register('guarantee', { 
                    min: { value: 0, message: '保证金不能为负' }
                  })}
                  isInvalid={!!errors.guarantee}
                  placeholder="选填，默认为0"
                />
                {errors.guarantee && (
                  <Form.Control.Feedback type="invalid">
                    {errors.guarantee.message}
                  </Form.Control.Feedback>
                )}
              </Col>
              <Col md={6}>
                <Form.Label>保证金处理方式</Form.Label>
                <Form.Select
                  {...register('guarantee_mode')}
                >
                  <option value="尾期冲抵">尾期冲抵</option>
                  <option value="按比例分摊">按比例分摊</option>
                  <option value="首期冲抵">首期冲抵</option>
                </Form.Select>
              </Col>
            </Row>
          </Modal.Body>
          <Modal.Footer>
            <Button variant="secondary" onClick={() => setShowModal(false)}>
              取消
            </Button>
            <Button variant="primary" type="submit">
              <i className="fas fa-plus me-1"></i>
              添加方案
            </Button>
          </Modal.Footer>
        </Form>
      </Modal>
    </div>
  );
};

export default CompareSchemes;
