import React, { useState } from 'react';
import { Card, Row, Col, Form, Button, Table, Alert, InputGroup, Badge } from 'react-bootstrap';
import { useForm } from 'react-hook-form';
import api from '../services/api';

const ReverseCalculation = ({ onError }) => {
  const [result, setResult] = useState(null);
  const [loading, setLoading] = useState(false);

  const { register, handleSubmit, watch, setValue, formState: { errors } } = useForm({
    defaultValues: {
      calculation_type: 'find_rate', // 'find_rate' 或 'find_irr'
      method: 'equal_annuity',
      pv: 1000000,
      periods: 36,
      frequency: 12,
      years: 3,
      equipment_price: 1000000,
      down_payment: 200000,
      guarantee: 0,
      guarantee_mode: '尾期冲抵',
      target_pmt: 30000, // 目标租金
      target_irr: 8.00   // 目标IRR(%)
    }
  });

  const watchEquipmentPrice = watch('equipment_price');
  const watchDownPayment = watch('down_payment');
  const watchFrequency = watch('frequency');
  const watchYears = watch('years');
  const watchCalculationType = watch('calculation_type');

  // 自动计算租赁本金
  React.useEffect(() => {
    const equipmentPrice = parseFloat(watchEquipmentPrice) || 0;
    const downPayment = parseFloat(watchDownPayment) || 0;
    const pv = equipmentPrice - downPayment;
    setValue('pv', pv);
  }, [watchEquipmentPrice, watchDownPayment, setValue]);

  // 根据年数和支付频率自动计算期数
  React.useEffect(() => {
    const years = parseFloat(watchYears) || 3;
    const frequency = parseInt(watchFrequency) || 12;
    const periods = years * frequency;
    setValue('periods', periods);
  }, [watchYears, watchFrequency, setValue]);

  const onSubmit = async (data) => {
    setLoading(true);
    try {
      const formData = {
        calculation_type: data.calculation_type,
        method: data.method,
        pv: parseFloat(data.pv),
        periods: parseInt(data.periods),
        frequency: parseInt(data.frequency),
        years: parseFloat(data.years),
        guarantee: parseFloat(data.guarantee) || 0,
        guarantee_mode: data.guarantee_mode,
        target_pmt: data.calculation_type === 'find_rate' ? parseFloat(data.target_pmt) : undefined,
        target_irr: data.calculation_type === 'find_irr' ? parseFloat(data.target_irr) / 100 : undefined
      };

      const response = await api.post('/reverse_calculate', formData);
      setResult(response.data.data);
    } catch (error) {
      onError(error.response?.data?.message || '反向计算失败');
    } finally {
      setLoading(false);
    }
  };

  const formatCurrency = (amount) => {
    return new Intl.NumberFormat('zh-CN', {
      style: 'currency',
      currency: 'CNY',
      minimumFractionDigits: 2
    }).format(amount || 0);
  };

  const formatPercent = (rate) => {
    return `${((rate || 0) * 100).toFixed(4)}%`;
  };

  const methodOptions = [
    { value: 'equal_annuity', label: '等额年金法（等额本息）' },
    { value: 'equal_principal', label: '等额本金法' },
    { value: 'flat_rate', label: '平息法' }
  ];

  const frequencyOptions = [
    { value: 12, label: '月付' },
    { value: 4, label: '季付' },
    { value: 2, label: '半年付' },
    { value: 1, label: '年付' }
  ];

  const guaranteeModeOptions = [
    { value: '尾期冲抵', label: '尾期冲抵' },
    { value: '按比例分摊', label: '按比例分摊' },
    { value: '首期冲抵', label: '首期冲抵' }
  ];

  return (
    <div>
      {/* 参数设置 */}
      <Card className="mb-4">
        <Card.Header className="bg-info text-white">
          <h5 className="mb-0">
            <i className="fas fa-exchange-alt me-2"></i>
            反向计算参数
          </h5>
        </Card.Header>
        <Card.Body>
          <Form onSubmit={handleSubmit(onSubmit)} className="reverse-calculation-form">
            {/* 计算类型选择 */}
            <div className="form-section">
              <h6><i className="fas fa-target me-2"></i>计算目标</h6>
              
              <Row className="mb-3">
                <Col>
                  <Form.Label>反向计算类型</Form.Label>
                  <Form.Select
                    {...register('calculation_type', { required: '请选择计算类型' })}
                    isInvalid={!!errors.calculation_type}
                  >
                    <option value="find_rate">根据目标租金推算年利率</option>
                    <option value="find_irr">根据目标IRR推算租金</option>
                  </Form.Select>
                  {errors.calculation_type && (
                    <Form.Control.Feedback type="invalid">
                      {errors.calculation_type.message}
                    </Form.Control.Feedback>
                  )}
                </Col>
              </Row>

              <Row className="mb-3">
                <Col>
                  <Form.Label>计算方法</Form.Label>
                  <Form.Select
                    {...register('method', { required: '请选择计算方法' })}
                    isInvalid={!!errors.method}
                  >
                    {methodOptions.map(option => (
                      <option key={option.value} value={option.value}>
                        {option.label}
                      </option>
                    ))}
                  </Form.Select>
                  {errors.method && (
                    <Form.Control.Feedback type="invalid">
                      {errors.method.message}
                    </Form.Control.Feedback>
                  )}
                </Col>
              </Row>
            </div>

            {/* 基础参数 */}
            <div className="form-section">
              <h6><i className="fas fa-cog me-2"></i>基础参数</h6>
              
              <Row className="mb-3">
                <Col md={6}>
                  <Form.Label>设备价格</Form.Label>
                  <InputGroup>
                    <Form.Control
                      type="number"
                      step="0.01"
                      {...register('equipment_price', { 
                        required: '请输入设备价格',
                        min: { value: 0, message: '设备价格不能为负' }
                      })}
                      isInvalid={!!errors.equipment_price}
                    />
                    <InputGroup.Text>元</InputGroup.Text>
                  </InputGroup>
                  {errors.equipment_price && (
                    <Form.Control.Feedback type="invalid">
                      {errors.equipment_price.message}
                    </Form.Control.Feedback>
                  )}
                </Col>
                <Col md={6}>
                  <Form.Label>首付款</Form.Label>
                  <InputGroup>
                    <Form.Control
                      type="number"
                      step="0.01"
                      {...register('down_payment', { 
                        min: { value: 0, message: '首付款不能为负' }
                      })}
                      isInvalid={!!errors.down_payment}
                    />
                    <InputGroup.Text>元</InputGroup.Text>
                  </InputGroup>
                  {errors.down_payment && (
                    <Form.Control.Feedback type="invalid">
                      {errors.down_payment.message}
                    </Form.Control.Feedback>
                  )}
                </Col>
              </Row>

              <Row className="mb-3">
                <Col md={6}>
                  <Form.Label>租赁本金</Form.Label>
                  <InputGroup>
                    <Form.Control
                      type="number"
                      {...register('pv')}
                      readOnly
                      className="bg-light"
                    />
                    <InputGroup.Text>元</InputGroup.Text>
                  </InputGroup>
                  <Form.Text className="text-muted">
                    自动计算：设备价格 - 首付款
                  </Form.Text>
                </Col>
                <Col md={6}>
                  <Form.Label>
                    {watchCalculationType === 'find_rate' ? '目标每期租金' : '目标IRR'}
                  </Form.Label>
                  <InputGroup>
                    {watchCalculationType === 'find_rate' ? (
                      <Form.Control
                        type="number"
                        step="0.01"
                        {...register('target_pmt', { 
                          required: '请输入目标租金',
                          min: { value: 1, message: '目标租金必须大于0' }
                        })}
                        isInvalid={!!errors.target_pmt}
                        placeholder="例如：30000"
                      />
                    ) : (
                      <Form.Control
                        type="number"
                        step="0.01"
                        {...register('target_irr', { 
                          required: '请输入目标IRR',
                          min: { value: 0.01, message: '目标IRR必须大于0' }
                        })}
                        isInvalid={!!errors.target_irr}
                        placeholder="例如：8.00"
                      />
                    )}
                    <InputGroup.Text>
                      {watchCalculationType === 'find_rate' ? '元' : '%'}
                    </InputGroup.Text>
                  </InputGroup>
                  {errors.target_pmt && (
                    <Form.Control.Feedback type="invalid">
                      {errors.target_pmt.message}
                    </Form.Control.Feedback>
                  )}
                  {errors.target_irr && (
                    <Form.Control.Feedback type="invalid">
                      {errors.target_irr.message}
                    </Form.Control.Feedback>
                  )}
                </Col>
              </Row>

              <Row className="mb-3">
                <Col md={4}>
                  <Form.Label>租赁年数</Form.Label>
                  <InputGroup>
                    <Form.Control
                      type="number"
                      step="0.1"
                      {...register('years', { 
                        required: '请输入租赁年数',
                        min: { value: 0.1, message: '租赁年数至少为0.1年' }
                      })}
                      isInvalid={!!errors.years}
                    />
                    <InputGroup.Text>年</InputGroup.Text>
                  </InputGroup>
                  {errors.years && (
                    <Form.Control.Feedback type="invalid">
                      {errors.years.message}
                    </Form.Control.Feedback>
                  )}
                </Col>
                <Col md={4}>
                  <Form.Label>支付频率</Form.Label>
                  <Form.Select
                    {...register('frequency', { required: '请选择支付频率' })}
                    isInvalid={!!errors.frequency}
                  >
                    {frequencyOptions.map(option => (
                      <option key={option.value} value={option.value}>
                        {option.label}
                      </option>
                    ))}
                  </Form.Select>
                  {errors.frequency && (
                    <Form.Control.Feedback type="invalid">
                      {errors.frequency.message}
                    </Form.Control.Feedback>
                  )}
                </Col>
                <Col md={4}>
                  <Form.Label>租赁期数</Form.Label>
                  <InputGroup>
                    <Form.Control
                      type="number"
                      {...register('periods')}
                      readOnly
                      className="bg-light"
                    />
                    <InputGroup.Text>期</InputGroup.Text>
                  </InputGroup>
                  <Form.Text className="text-muted">
                    根据年数和频率自动计算
                  </Form.Text>
                </Col>
              </Row>
            </div>

            {/* 保证金设置 */}
            <div className="form-section">
              <h6><i className="fas fa-shield-alt me-2"></i>保证金设置</h6>
              
              <Row className="mb-3">
                <Col md={6}>
                  <Form.Label>保证金金额</Form.Label>
                  <InputGroup>
                    <Form.Control
                      type="number"
                      step="0.01"
                      {...register('guarantee', { 
                        min: { value: 0, message: '保证金不能为负' }
                      })}
                      isInvalid={!!errors.guarantee}
                    />
                    <InputGroup.Text>元</InputGroup.Text>
                  </InputGroup>
                  {errors.guarantee && (
                    <Form.Control.Feedback type="invalid">
                      {errors.guarantee.message}
                    </Form.Control.Feedback>
                  )}
                </Col>
                <Col md={6}>
                  <Form.Label>冲抵模式</Form.Label>
                  <Form.Select
                    {...register('guarantee_mode')}
                  >
                    {guaranteeModeOptions.map(option => (
                      <option key={option.value} value={option.value}>
                        {option.label}
                      </option>
                    ))}
                  </Form.Select>
                </Col>
              </Row>
            </div>

            {/* 计算按钮 */}
            <Row className="mb-3">
              <Col>
                <div className="d-grid">
                  <Button 
                    type="submit" 
                    variant="info" 
                    size="lg"
                    disabled={loading}
                    className="btn-reverse-calculate"
                  >
                    {loading ? (
                      <>
                        <i className="fas fa-spinner fa-spin me-2"></i>
                        计算中...
                      </>
                    ) : (
                      <>
                        <i className="fas fa-exchange-alt me-2"></i>
                        开始反向计算
                      </>
                    )}
                  </Button>
                </div>
              </Col>
            </Row>
          </Form>
        </Card.Body>
      </Card>

      {/* 计算结果 */}
      {result && (
        <Card>
          <Card.Header className="bg-success text-white">
            <h5 className="mb-0">
              <i className="fas fa-check-circle me-2"></i>
              反向计算结果
            </h5>
          </Card.Header>
          <Card.Body>
            <Alert variant="info">
              <Alert.Heading>
                <i className="fas fa-info-circle me-2"></i>
                计算结果说明
              </Alert.Heading>
              {watchCalculationType === 'find_rate' ? (
                <p>根据您设置的目标租金，系统计算出对应的年利率和相关财务指标。</p>
              ) : (
                <p>根据您设置的目标IRR，系统计算出对应的租金和相关财务指标。</p>
              )}
            </Alert>

            <Row>
              <Col lg={6}>
                <Card className="h-100">
                  <Card.Header className="bg-light">
                    <h6 className="mb-0">核心指标</h6>
                  </Card.Header>
                  <Card.Body>
                    <Table borderless>
                      <tbody>
                        {watchCalculationType === 'find_rate' ? (
                          <>
                            <tr>
                              <td><strong>计算得出的年利率：</strong></td>
                              <td className="text-end">
                                <Badge bg="primary" className="fs-6">
                                  {formatPercent(result.calculated_rate)}
                                </Badge>
                              </td>
                            </tr>
                            <tr>
                              <td><strong>目标每期租金：</strong></td>
                              <td className="text-end">{formatCurrency(result.target_pmt)}</td>
                            </tr>
                            <tr>
                              <td><strong>实际计算租金：</strong></td>
                              <td className="text-end">{formatCurrency(result.actual_pmt)}</td>
                            </tr>
                            <tr>
                              <td><strong>误差：</strong></td>
                              <td className="text-end">
                                <span className={Math.abs(result.error || 0) < 0.01 ? 'text-success' : 'text-warning'}>
                                  {formatCurrency(Math.abs(result.error || 0))}
                                </span>
                              </td>
                            </tr>
                          </>
                        ) : (
                          <>
                            <tr>
                              <td><strong>计算得出的每期租金：</strong></td>
                              <td className="text-end">
                                <Badge bg="primary" className="fs-6">
                                  {formatCurrency(result.calculated_pmt)}
                                </Badge>
                              </td>
                            </tr>
                            <tr>
                              <td><strong>目标IRR：</strong></td>
                              <td className="text-end">{formatPercent(result.target_irr)}</td>
                            </tr>
                            <tr>
                              <td><strong>实际计算IRR：</strong></td>
                              <td className="text-end">{formatPercent(result.actual_irr)}</td>
                            </tr>
                            <tr>
                              <td><strong>误差：</strong></td>
                              <td className="text-end">
                                <span className={Math.abs(result.irr_error || 0) < 0.0001 ? 'text-success' : 'text-warning'}>
                                  {formatPercent(Math.abs(result.irr_error || 0))}
                                </span>
                              </td>
                            </tr>
                          </>
                        )}
                        <tr>
                          <td><strong>总利息：</strong></td>
                          <td className="text-end">{formatCurrency(result.total_interest)}</td>
                        </tr>
                        <tr>
                          <td><strong>总支付：</strong></td>
                          <td className="text-end">{formatCurrency(result.total_payment)}</td>
                        </tr>
                      </tbody>
                    </Table>
                  </Card.Body>
                </Card>
              </Col>
              <Col lg={6}>
                <Card className="h-100">
                  <Card.Header className="bg-light">
                    <h6 className="mb-0">计算参数</h6>
                  </Card.Header>
                  <Card.Body>
                    <Table borderless>
                      <tbody>
                        <tr>
                          <td><strong>计算方法：</strong></td>
                          <td className="text-end">{methodOptions.find(m => m.value === result.method)?.label}</td>
                        </tr>
                        <tr>
                          <td><strong>租赁本金：</strong></td>
                          <td className="text-end">{formatCurrency(result.pv)}</td>
                        </tr>
                        <tr>
                          <td><strong>租赁期数：</strong></td>
                          <td className="text-end">{result.periods}期</td>
                        </tr>
                        <tr>
                          <td><strong>支付频率：</strong></td>
                          <td className="text-end">{frequencyOptions.find(f => f.value === result.frequency)?.label}</td>
                        </tr>
                        {result.guarantee > 0 && (
                          <>
                            <tr>
                              <td><strong>保证金：</strong></td>
                              <td className="text-end">{formatCurrency(result.guarantee)}</td>
                            </tr>
                            <tr>
                              <td><strong>冲抵模式：</strong></td>
                              <td className="text-end">{result.guarantee_mode}</td>
                            </tr>
                          </>
                        )}
                      </tbody>
                    </Table>
                  </Card.Body>
                </Card>
              </Col>
            </Row>

            {result.iterations && (
              <Alert variant="secondary" className="mt-3">
                <small>
                  <i className="fas fa-info-circle me-1"></i>
                  迭代计算完成，共进行了 {result.iterations} 次迭代，精度误差在可接受范围内。
                </small>
              </Alert>
            )}
          </Card.Body>
        </Card>
      )}
    </div>
  );
};

export default ReverseCalculation;
