import React, { useEffect } from 'react';
import { Form, Button, Row, Col, Card, InputGroup } from 'react-bootstrap';
import { useForm } from 'react-hook-form';

const CalculatorForm = ({ onCalculate, loading, onAddToCompare }) => {
  const { register, handleSubmit, watch, setValue, formState: { errors } } = useForm({
    defaultValues: {
      method: 'equal_annuity',
      pv: 1000000,
      annual_rate: 8.00,
      periods: 36,
      frequency: 12,
      guarantee: 0,
      guarantee_mode: '尾期冲抵',
      equipment_price: 1000000,
      down_payment: 200000,
      years: 3
    }
  });

  const watchMethod = watch('method');
  const watchEquipmentPrice = watch('equipment_price');
  const watchDownPayment = watch('down_payment');
  const watchFrequency = watch('frequency');
  const watchYears = watch('years');

  // 自动计算租赁本金
  useEffect(() => {
    const equipmentPrice = parseFloat(watchEquipmentPrice) || 0;
    const downPayment = parseFloat(watchDownPayment) || 0;
    const pv = equipmentPrice - downPayment;
    setValue('pv', pv);
  }, [watchEquipmentPrice, watchDownPayment, setValue]);

  // 根据年数和支付频率自动计算期数
  useEffect(() => {
    const years = parseFloat(watchYears) || 3;
    const frequency = parseInt(watchFrequency) || 12;
    // 频率表示每年的支付次数，所以期数 = 年数 × 频率
    const periods = years * frequency;
    setValue('periods', periods);
  }, [watchYears, watchFrequency, setValue]);

  const onSubmit = (data) => {
    // 数据转换和验证
    const formData = {
      ...data,
      pv: parseFloat(data.pv),
      annual_rate: parseFloat(data.annual_rate) / 100, // 转换百分比为小数
      periods: parseInt(data.periods),
      frequency: parseInt(data.frequency),
      guarantee: parseFloat(data.guarantee) || 0,
      years: parseFloat(data.years) || data.periods / data.frequency
    };

    // 浮动利率特殊处理
    if (data.method === 'floating_rate' && data.rate_reset_schedule) {
      try {
        formData.rate_reset_schedule = JSON.parse(data.rate_reset_schedule);
      } catch (e) {
        formData.rate_reset_schedule = [];
      }
    }

    onCalculate(formData);
  };

  const methodOptions = [
    { value: 'equal_annuity', label: '等额年金法（等额本息）' },
    { value: 'equal_principal', label: '等额本金法' },
    { value: 'flat_rate', label: '平息法' },
    { value: 'floating_rate', label: '浮动利率法' }
  ];

  const frequencyOptions = [
    { value: 12, label: '月付' },
    { value: 4, label: '季付' },
    { value: 2, label: '半年付' },
    { value: 1, label: '年付' }
  ];

  const guaranteeModeOptions = [
    { value: '尾期冲抵', label: '尾期冲抵' },
    { value: '按比例分摊', label: '按比例分摊' },
    { value: '首期冲抵', label: '首期冲抵' }
  ];

  return (
    <Form onSubmit={handleSubmit(onSubmit)} className="calculator-form">
      {/* 基础参数 */}
      <div className="form-section">
        <h6><i className="fas fa-cog me-2"></i>基础参数</h6>
        
        <Row className="mb-3">
          <Col>
            <Form.Label>计算方法</Form.Label>
            <Form.Select
              {...register('method', { required: '请选择计算方法' })}
              isInvalid={!!errors.method}
            >
              {methodOptions.map(option => (
                <option key={option.value} value={option.value}>
                  {option.label}
                </option>
              ))}
            </Form.Select>
            {errors.method && (
              <Form.Control.Feedback type="invalid">
                {errors.method.message}
              </Form.Control.Feedback>
            )}
          </Col>
        </Row>

        <Row className="mb-3">
          <Col md={6}>
            <Form.Label>设备价格</Form.Label>
            <InputGroup>
              <Form.Control
                type="number"
                step="0.01"
                {...register('equipment_price', { 
                  required: '请输入设备价格',
                  min: { value: 0, message: '设备价格不能为负' }
                })}
                isInvalid={!!errors.equipment_price}
              />
              <InputGroup.Text>元</InputGroup.Text>
            </InputGroup>
            {errors.equipment_price && (
              <Form.Control.Feedback type="invalid">
                {errors.equipment_price.message}
              </Form.Control.Feedback>
            )}
          </Col>
          <Col md={6}>
            <Form.Label>首付款</Form.Label>
            <InputGroup>
              <Form.Control
                type="number"
                step="0.01"
                {...register('down_payment', { 
                  min: { value: 0, message: '首付款不能为负' }
                })}
                isInvalid={!!errors.down_payment}
              />
              <InputGroup.Text>元</InputGroup.Text>
            </InputGroup>
            {errors.down_payment && (
              <Form.Control.Feedback type="invalid">
                {errors.down_payment.message}
              </Form.Control.Feedback>
            )}
          </Col>
        </Row>

        <Row className="mb-3">
          <Col>
            <Form.Label>租赁本金（PV）</Form.Label>
            <InputGroup>
              <Form.Control
                type="number"
                step="0.01"
                {...register('pv', { 
                  required: '请输入租赁本金',
                  min: { value: 1, message: '租赁本金必须大于0' }
                })}
                isInvalid={!!errors.pv}
                readOnly
              />
              <InputGroup.Text>元</InputGroup.Text>
            </InputGroup>
            {errors.pv && (
              <Form.Control.Feedback type="invalid">
                {errors.pv.message}
              </Form.Control.Feedback>
            )}
          </Col>
        </Row>

        <Row className="mb-3">
          <Col md={6}>
            <Form.Label>
              {watchMethod === 'flat_rate' ? '平息年利率' : '年利率'}
            </Form.Label>
            <InputGroup>
              <Form.Control
                type="number"
                step="0.01"
                {...register('annual_rate', { 
                  required: '请输入年利率',
                  min: { value: 0.01, message: '年利率必须大于0' },
                  max: { value: 100, message: '年利率不能超过100%' }
                })}
                isInvalid={!!errors.annual_rate}
              />
              <InputGroup.Text>%</InputGroup.Text>
            </InputGroup>
            <Form.Text className="text-muted">
              例如：8%直接填写8.00
            </Form.Text>
            {errors.annual_rate && (
              <Form.Control.Feedback type="invalid">
                {errors.annual_rate.message}
              </Form.Control.Feedback>
            )}
          </Col>
          <Col md={6}>
            <Form.Label>支付频率</Form.Label>
            <Form.Select
              {...register('frequency', { required: '请选择支付频率' })}
              isInvalid={!!errors.frequency}
            >
              {frequencyOptions.map(option => (
                <option key={option.value} value={option.value}>
                  {option.label}
                </option>
              ))}
            </Form.Select>
            {errors.frequency && (
              <Form.Control.Feedback type="invalid">
                {errors.frequency.message}
              </Form.Control.Feedback>
            )}
          </Col>
        </Row>

        <Row className="mb-3">
          <Col md={6}>
            <Form.Label>租赁年数</Form.Label>
            <InputGroup>
              <Form.Control
                type="number"
                step="0.1"
                {...register('years', { 
                  required: '请输入租赁年数',
                  min: { value: 0.1, message: '租赁年数至少为0.1年' }
                })}
                isInvalid={!!errors.years}
              />
              <InputGroup.Text>年</InputGroup.Text>
            </InputGroup>
            {errors.years && (
              <Form.Control.Feedback type="invalid">
                {errors.years.message}
              </Form.Control.Feedback>
            )}
          </Col>
          <Col md={6}>
            <Form.Label>租赁期数（自动计算）</Form.Label>
            <InputGroup>
              <Form.Control
                type="number"
                {...register('periods')}
                readOnly
              />
              <InputGroup.Text>期</InputGroup.Text>
            </InputGroup>
            <Form.Text className="text-muted">
              根据租赁年数和支付频率自动计算
            </Form.Text>
          </Col>
        </Row>

      </div>

      {/* 保证金设置 */}
      <div className="form-section">
        <h6><i className="fas fa-shield-alt me-2"></i>保证金设置</h6>
        
        <Row className="mb-3">
          <Col md={6}>
            <Form.Label>保证金金额</Form.Label>
            <InputGroup>
              <Form.Control
                type="number"
                step="0.01"
                {...register('guarantee', { 
                  min: { value: 0, message: '保证金不能为负' }
                })}
                isInvalid={!!errors.guarantee}
              />
              <InputGroup.Text>元</InputGroup.Text>
            </InputGroup>
            {errors.guarantee && (
              <Form.Control.Feedback type="invalid">
                {errors.guarantee.message}
              </Form.Control.Feedback>
            )}
          </Col>
          <Col md={6}>
            <Form.Label>冲抵模式</Form.Label>
            <Form.Select
              {...register('guarantee_mode')}
            >
              {guaranteeModeOptions.map(option => (
                <option key={option.value} value={option.value}>
                  {option.label}
                </option>
              ))}
            </Form.Select>
          </Col>
        </Row>
      </div>

      {/* 浮动利率特殊设置 */}
      {watchMethod === 'floating_rate' && (
        <div className="form-section">
          <h6><i className="fas fa-chart-line me-2"></i>浮动利率设置</h6>
          
          <Row className="mb-3">
            <Col>
              <Form.Label>利率重置计划（JSON格式）</Form.Label>
              <Form.Control
                as="textarea"
                rows={3}
                {...register('rate_reset_schedule')}
                placeholder='[{"period": 6, "new_rate": 0.065}, {"period": 12, "new_rate": 0.07}]'
              />
              <Form.Text className="text-muted">
                示例：第6期重置为6.5%，第12期重置为7%
              </Form.Text>
            </Col>
          </Row>
        </div>
      )}

      {/* 计算按钮 */}
      <Row className="mb-3">
        <Col md={8}>
          <div className="d-grid">
            <Button 
              type="submit" 
              variant="primary" 
              size="lg"
              disabled={loading}
              className="btn-calculate"
            >
              {loading ? (
                <>
                  <i className="fas fa-spinner fa-spin me-2"></i>
                  计算中...
                </>
              ) : (
                <>
                  <i className="fas fa-calculator me-2"></i>
                  开始计算
                </>
              )}
            </Button>
          </div>
        </Col>
        <Col md={4}>
          <div className="d-grid">
            <Button 
              type="button"
              variant="outline-success" 
              size="lg"
              onClick={() => {
                const formData = {
                  method: watch('method'),
                  pv: parseFloat(watch('pv')),
                  annual_rate: parseFloat(watch('annual_rate')) / 100,
                  periods: parseInt(watch('periods')),
                  frequency: parseInt(watch('frequency')),
                  guarantee: parseFloat(watch('guarantee')) || 0,
                  guarantee_mode: watch('guarantee_mode'),
                  years: parseFloat(watch('years'))
                };
                if (onAddToCompare) {
                  onAddToCompare(formData);
                }
              }}
              className="btn-add-compare"
            >
              <i className="fas fa-plus me-2"></i>
              添加到对比
            </Button>
          </div>
        </Col>
      </Row>

      {/* 计算说明 */}
      <Card className="mt-3" bg="light">
        <Card.Body>
          <Card.Title className="h6">
            <i className="fas fa-info-circle me-2"></i>
            算法说明
          </Card.Title>
          <Card.Text className="small">
            {watchMethod === 'equal_annuity' && 
              '等额年金法：每期租金相等，包含逐期递减的利息和逐期递增的本金。公式：PMT = PV × [i × (1+i)^n] / [(1+i)^n - 1]'
            }
            {watchMethod === 'equal_principal' && 
              '等额本金法：每期偿还本金相等，利息根据剩余本金计算，租金逐期递减。'
            }
            {watchMethod === 'flat_rate' && 
              '平息法：利息按初始本金计算，总利息均摊到各期。实际利率高于名义利率。'
            }
            {watchMethod === 'floating_rate' && 
              '浮动利率法：利率定期重置，租金在重置日重新计算。适用于LPR等基准利率加点模式。'
            }
          </Card.Text>
        </Card.Body>
      </Card>
    </Form>
  );
};

export default CalculatorForm;
