import React, { useState, useRef, useEffect } from 'react';
import { Card, Row, Col, Form, Button, Table, Alert } from 'react-bootstrap';
import { useForm } from 'react-hook-form';
import Chart from 'chart.js/auto';
import api from '../services/api';

const SensitivityAnalysis = ({ onError }) => {
  const [result, setResult] = useState(null);
  const [loading, setLoading] = useState(false);
  const chartRef = useRef(null);
  const chartInstance = useRef(null);

  const { register, handleSubmit, watch, setValue, formState: { errors } } = useForm({
    defaultValues: {
      method: 'equal_annuity',
      pv: 1000000,
      annual_rate: 8.00,  // 改为百分比格式
      periods: 36,
      frequency: 12,
      years: 3,
      equipment_price: 1000000,
      down_payment: 200000,
      guarantee: 0,
      guarantee_mode: '尾期冲抵',
      rate_variation: 1.00,  // 改为百分比格式
      period_variation: 6,
      pv_variation: 100000
    }
  });

  const watchEquipmentPrice = watch('equipment_price');
  const watchDownPayment = watch('down_payment');
  const watchFrequency = watch('frequency');
  const watchYears = watch('years');

  // 自动计算租赁本金
  useEffect(() => {
    const equipmentPrice = parseFloat(watchEquipmentPrice) || 0;
    const downPayment = parseFloat(watchDownPayment) || 0;
    const pv = equipmentPrice - downPayment;
    setValue('pv', pv);
  }, [watchEquipmentPrice, watchDownPayment, setValue]);

  // 根据年数和支付频率自动计算期数
  useEffect(() => {
    const years = parseFloat(watchYears) || 3;
    const frequency = parseInt(watchFrequency) || 12;
    const periods = years * frequency;
    setValue('periods', periods);
  }, [watchYears, watchFrequency, setValue]);

  // 创建敏感性分析图表
  const createChart = (data) => {
    if (chartInstance.current) {
      chartInstance.current.destroy();
    }

    if (!chartRef.current) return;

    const ctx = chartRef.current.getContext('2d');
    chartInstance.current = new Chart(ctx, {
      type: 'bar',
      data: {
        labels: data.map(item => item.parameter),
        datasets: [{
          label: '敏感度系数',
          data: data.map(item => Math.abs(item.sensitivity)),
          backgroundColor: data.map(item => {
            const abs = Math.abs(item.sensitivity);
            if (abs > 1) return 'rgba(220, 53, 69, 0.8)';
            if (abs > 0.5) return 'rgba(255, 193, 7, 0.8)';
            return 'rgba(40, 167, 69, 0.8)';
          }),
          borderColor: data.map(item => {
            const abs = Math.abs(item.sensitivity);
            if (abs > 1) return 'rgba(220, 53, 69, 1)';
            if (abs > 0.5) return 'rgba(255, 193, 7, 1)';
            return 'rgba(40, 167, 69, 1)';
          }),
          borderWidth: 1
        }]
      },
      options: {
        responsive: true,
        maintainAspectRatio: false,
        scales: {
          y: {
            beginAtZero: true,
            title: {
              display: true,
              text: '敏感度系数 (|敏感度|)'
            }
          },
          x: {
            title: {
              display: true,
              text: '参数'
            }
          }
        },
        plugins: {
          legend: {
            display: true,
            position: 'top'
          },
          title: {
            display: true,
            text: '敏感性分析结果'
          }
        }
      }
    });
  };

  const onSubmit = async (data) => {
    setLoading(true);
    try {
      // 将百分比转换为小数
      const processedData = {
        ...data,
        annual_rate: data.annual_rate / 100,  // 转换为小数
        rate_variation: data.rate_variation / 100  // 转换为小数
      };

      const response = await api.post('/sensitivity_analysis', processedData);
      setResult(response.data);
      
      // 创建图表
      if (response.data.sensitivity_analysis) {
        setTimeout(() => {
          createChart(response.data.sensitivity_analysis);
        }, 100);
      }
    } catch (error) {
      console.error('敏感性分析失败:', error);
      onError?.(error.response?.data?.error || '敏感性分析失败');
    } finally {
      setLoading(false);
    }
  };

  // 清理图表
  useEffect(() => {
    return () => {
      if (chartInstance.current) {
        chartInstance.current.destroy();
      }
    };
  }, []);

  return (
    <div>
      <Card className="mb-4">
        <Card.Header>
          <h5 className="mb-0">
            <i className="fas fa-chart-line me-2"></i>
            敏感性分析
          </h5>
        </Card.Header>
        <Card.Body>
          <Form onSubmit={handleSubmit(onSubmit)}>
            {/* 基本参数 */}
            <div className="form-section">
              <h6><i className="fas fa-cog me-2"></i>基本参数</h6>
              
              <Row className="mb-3">
                <Col md={6}>
                  <Form.Label>计算方法</Form.Label>
                  <Form.Select
                    {...register('method', { required: '请选择计算方法' })}
                    isInvalid={!!errors.method}
                  >
                    <option value="equal_annuity">等额本息</option>
                    <option value="equal_principal">等额本金</option>
                  </Form.Select>
                  {errors.method && (
                    <Form.Control.Feedback type="invalid">
                      {errors.method.message}
                    </Form.Control.Feedback>
                  )}
                </Col>
              </Row>

              <Row className="mb-3">
                <Col md={6}>
                  <Form.Label>设备价格</Form.Label>
                  <Form.Control
                    type="number"
                    step="1000"
                    {...register('equipment_price', { 
                      required: '请输入设备价格',
                      min: { value: 1, message: '设备价格必须大于0' }
                    })}
                    isInvalid={!!errors.equipment_price}
                  />
                  {errors.equipment_price && (
                    <Form.Control.Feedback type="invalid">
                      {errors.equipment_price.message}
                    </Form.Control.Feedback>
                  )}
                </Col>
                <Col md={6}>
                  <Form.Label>首付款</Form.Label>
                  <Form.Control
                    type="number"
                    step="1000"
                    {...register('down_payment', { 
                      required: '请输入首付款',
                      min: { value: 0, message: '首付款不能为负数' }
                    })}
                    isInvalid={!!errors.down_payment}
                  />
                  {errors.down_payment && (
                    <Form.Control.Feedback type="invalid">
                      {errors.down_payment.message}
                    </Form.Control.Feedback>
                    )}
                </Col>
              </Row>

              <Row className="mb-3">
                <Col md={6}>
                  <Form.Label>租赁本金</Form.Label>
                  <Form.Control
                    type="number"
                    {...register('pv')}
                    readOnly
                    className="bg-light"
                  />
                  <Form.Text className="text-muted">
                    自动计算：设备价格 - 首付款
                  </Form.Text>
                </Col>
                <Col md={6}>
                  <Form.Label>年利率 (%)</Form.Label>
                  <Form.Control
                    type="number"
                    step="0.01"
                    {...register('annual_rate', { 
                      required: '请输入年利率',
                      min: { value: 0.01, message: '年利率必须大于0' }
                    })}
                    isInvalid={!!errors.annual_rate}
                  />
                  {errors.annual_rate && (
                    <Form.Control.Feedback type="invalid">
                      {errors.annual_rate.message}
                    </Form.Control.Feedback>
                  )}
                </Col>
              </Row>

              <Row className="mb-3">
                <Col md={4}>
                  <Form.Label>租赁年数</Form.Label>
                  <Form.Control
                    type="number"
                    step="0.1"
                    {...register('years', { 
                      required: '请输入租赁年数',
                      min: { value: 0.1, message: '租赁年数必须大于0' }
                    })}
                    isInvalid={!!errors.years}
                  />
                  {errors.years && (
                    <Form.Control.Feedback type="invalid">
                      {errors.years.message}
                    </Form.Control.Feedback>
                  )}
                </Col>
                <Col md={4}>
                  <Form.Label>支付频率</Form.Label>
                  <Form.Select
                    {...register('frequency', { required: '请选择支付频率' })}
                    isInvalid={!!errors.frequency}
                  >
                    <option value="12">月付</option>
                    <option value="4">季付</option>
                    <option value="2">半年付</option>
                    <option value="1">年付</option>
                  </Form.Select>
                  {errors.frequency && (
                    <Form.Control.Feedback type="invalid">
                      {errors.frequency.message}
                    </Form.Control.Feedback>
                  )}
                </Col>
                <Col md={4}>
                  <Form.Label>租赁期数</Form.Label>
                  <Form.Control
                    type="number"
                    {...register('periods')}
                    readOnly
                    className="bg-light"
                  />
                  <Form.Text className="text-muted">
                    自动计算
                  </Form.Text>
                </Col>
              </Row>
            </div>

            {/* 保证金设置 */}
            <div className="form-section">
              <h6><i className="fas fa-shield-alt me-2"></i>保证金设置</h6>
              
              <Row className="mb-3">
                <Col md={6}>
                  <Form.Label>保证金金额</Form.Label>
                  <Form.Control
                    type="number"
                    step="1000"
                    {...register('guarantee', { 
                      min: { value: 0, message: '保证金不能为负数' }
                    })}
                    isInvalid={!!errors.guarantee}
                  />
                  {errors.guarantee && (
                    <Form.Control.Feedback type="invalid">
                      {errors.guarantee.message}
                    </Form.Control.Feedback>
                  )}
                </Col>
                <Col md={6}>
                  <Form.Label>冲抵模式</Form.Label>
                  <Form.Select
                    {...register('guarantee_mode')}
                  >
                    <option value="尾期冲抵">尾期冲抵</option>
                    <option value="按比例分摊">按比例分摊</option>
                    <option value="首期冲抵">首期冲抵</option>
                  </Form.Select>
                </Col>
              </Row>
            </div>

            {/* 敏感性分析参数 */}
            <div className="form-section">
              <h6><i className="fas fa-sliders-h me-2"></i>敏感性分析设置</h6>
              
              <Row className="mb-3">
                <Col md={4}>
                  <Form.Label>利率变动幅度 (%)</Form.Label>
                  <Form.Control
                    type="number"
                    step="0.01"
                    {...register('rate_variation', { 
                      required: '请输入利率变动幅度',
                      min: { value: 0.01, message: '变动幅度必须大于0' }
                    })}
                    isInvalid={!!errors.rate_variation}
                    placeholder="例如：1.00"
                  />
                  <Form.Text className="text-muted">±变动幅度，例如1.00表示±1%</Form.Text>
                  {errors.rate_variation && (
                    <Form.Control.Feedback type="invalid">
                      {errors.rate_variation.message}
                    </Form.Control.Feedback>
                  )}
                </Col>
                <Col md={4}>
                  <Form.Label>期限变动幅度（期）</Form.Label>
                  <Form.Control
                    type="number"
                    {...register('period_variation', { 
                      required: '请输入期限变动幅度',
                      min: { value: 1, message: '变动幅度至少为1期' }
                    })}
                    isInvalid={!!errors.period_variation}
                  />
                  <Form.Text className="text-muted">±变动期数</Form.Text>
                  {errors.period_variation && (
                    <Form.Control.Feedback type="invalid">
                      {errors.period_variation.message}
                    </Form.Control.Feedback>
                  )}
                </Col>
                <Col md={4}>
                  <Form.Label>本金变动幅度（元）</Form.Label>
                  <Form.Control
                    type="number"
                    step="1000"
                    {...register('pv_variation', { 
                      required: '请输入本金变动幅度',
                      min: { value: 1000, message: '变动幅度至少为1000元' }
                    })}
                    isInvalid={!!errors.pv_variation}
                  />
                  <Form.Text className="text-muted">±变动金额</Form.Text>
                  {errors.pv_variation && (
                    <Form.Control.Feedback type="invalid">
                      {errors.pv_variation.message}
                    </Form.Control.Feedback>
                  )}
                </Col>
              </Row>
            </div>

            <div className="text-center">
              <Button
                type="submit"
                variant="primary"
                size="lg"
                disabled={loading}
              >
                {loading ? (
                  <>
                    <i className="fas fa-spinner fa-spin me-2"></i>
                    分析中...
                  </>
                ) : (
                  <>
                    <i className="fas fa-chart-line me-2"></i>
                    开始敏感性分析
                  </>
                )}
              </Button>
            </div>
          </Form>
        </Card.Body>
      </Card>

      {/* 分析结果 */}
      {result && (
        <>
          {/* 基准结果 */}
          <Card className="mb-4">
            <Card.Header>
              <h5 className="mb-0">
                <i className="fas fa-bullseye me-2"></i>
                基准计算结果
              </h5>
            </Card.Header>
            <Card.Body>
              <Row>
                <Col md={3}>
                  <div className="text-center p-3 bg-light rounded">
                    <h6 className="text-muted mb-1">月租金</h6>
                    <h4 className="mb-0 text-primary">
                      ¥{result.base_payment?.toLocaleString() || 0}
                    </h4>
                  </div>
                </Col>
                <Col md={3}>
                  <div className="text-center p-3 bg-light rounded">
                    <h6 className="text-muted mb-1">总租金</h6>
                    <h4 className="mb-0 text-success">
                      ¥{result.base_total_payment?.toLocaleString() || 0}
                    </h4>
                  </div>
                </Col>
                <Col md={3}>
                  <div className="text-center p-3 bg-light rounded">
                    <h6 className="text-muted mb-1">内部收益率</h6>
                    <h4 className="mb-0 text-info">
                      {((result.base_irr || 0) * 100).toFixed(2)}%
                    </h4>
                  </div>
                </Col>
                <Col md={3}>
                  <div className="text-center p-3 bg-light rounded">
                    <h6 className="text-muted mb-1">利息总额</h6>
                    <h4 className="mb-0 text-warning">
                      ¥{result.base_total_interest?.toLocaleString() || 0}
                    </h4>
                  </div>
                </Col>
              </Row>
            </Card.Body>
          </Card>

          {/* 敏感性分析表格 */}
          {result.sensitivity_analysis && (
            <Card className="mb-4">
              <Card.Header>
                <h5 className="mb-0">
                  <i className="fas fa-table me-2"></i>
                  敏感性分析结果
                </h5>
              </Card.Header>
              <Card.Body>
                <div className="table-responsive">
                  <Table striped bordered hover>
                    <thead>
                      <tr>
                        <th>参数</th>
                        <th>变动</th>
                        <th>月租金</th>
                        <th>月租金变化</th>
                        <th>变化率</th>
                        <th>敏感度系数</th>
                      </tr>
                    </thead>
                    <tbody>
                      {result.sensitivity_analysis.map((item, index) => (
                        <tr key={index}>
                          <td>{item.parameter}</td>
                          <td className={item.change > 0 ? 'text-success' : 'text-danger'}>
                            {item.change > 0 ? '+' : ''}{item.change}
                            {item.parameter.includes('利率') ? '%' : 
                             item.parameter.includes('期') ? '期' : '元'}
                          </td>
                          <td>¥{item.payment?.toLocaleString() || 0}</td>
                          <td className={item.payment_change > 0 ? 'text-danger' : 'text-success'}>
                            {item.payment_change > 0 ? '+' : ''}¥{item.payment_change?.toLocaleString() || 0}
                          </td>
                          <td className={item.change_rate > 0 ? 'text-danger' : 'text-success'}>
                            {item.change_rate > 0 ? '+' : ''}{(item.change_rate || 0).toFixed(2)}%
                          </td>
                          <td>
                            <span className={`badge ${Math.abs(item.sensitivity || 0) > 1 ? 'bg-danger' : 
                                                     Math.abs(item.sensitivity || 0) > 0.5 ? 'bg-warning' : 'bg-success'}`}>
                              {(item.sensitivity || 0).toFixed(2)}
                            </span>
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </Table>
                </div>
              </Card.Body>
            </Card>
          )}

          {/* 敏感性分析图表 */}
          <Card className="mb-4">
            <Card.Header>
              <h5 className="mb-0">
                <i className="fas fa-chart-bar me-2"></i>
                敏感性分析图表
              </h5>
            </Card.Header>
            <Card.Body>
              <div className="chart-container" style={{ height: '400px' }}>
                <canvas 
                  ref={chartRef}
                  style={{ maxHeight: '100%', width: '100%' }}
                ></canvas>
              </div>
            </Card.Body>
          </Card>

          {/* 风险分析与建议 */}
          {result.sensitivity_analysis && (
            <Alert variant="info">
              <Row>
                <Col md={6}>
                  <h6>高敏感性参数：</h6>
                  <ul className="mb-0">
                    {result.sensitivity_analysis.map((item, index) => {
                      if (Math.abs(item.sensitivity || 0) > 1) {
                        return (
                          <li key={index}>
                            <strong>{item.parameter}</strong>: 敏感度系数 {(item.sensitivity || 0).toFixed(2)}
                          </li>
                        );
                      }
                      return null;
                    })}
                  </ul>
                </Col>
                <Col md={6}>
                  <h6>风险提示：</h6>
                  <p className="mb-0">
                    高敏感性参数的变动对租金影响较大，在实际业务中需要重点关注这些参数的变化。
                    建议建立动态定价机制和风险预警系统。
                  </p>
                </Col>
              </Row>
            </Alert>
          )}
        </>
      )}
    </div>
  );
};

export default SensitivityAnalysis;
